import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import BlockItem from '../components/BlockItem/BlockItem';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));

function CenteredGrid() {
    const classes = useStyles();

    return (
        <div container spacing={1} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <Grid container item xs={9} spacing={3} style={{padding: '0 100px'}}>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                <Grid item xs={4} >
                    <BlockItem/>
                </Grid>
                
            </Grid>
        </div>
    );
}

export default CenteredGrid;