import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import CameraAltIcon from '@material-ui/icons/CameraAlt';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
}));

function DenseAppBar() {
    const classes = useStyles();

    return (
        <div className={classes.root} style={{ boxShadow: "0 0 10px" }}>
            <AppBar position="static" style={{ background: "#90CAF9", padding: "7px 0", boxShadow: "0 0 10px" }}>
                <Toolbar variant="dense" style={{ display: "flex", alignItems: "center"}}>
                    <IconButton edge="start" className={classes.menuButton} aria-label="menu">
                        <CameraAltIcon style={{ color: 'black', fontSize: 24  }}  />
                    </IconButton>
                    <Typography variant="h6" style={{ color: 'black', fontSize: 20, marginLeft: "-13px"  }}>
                        Album layout
          </Typography>
                </Toolbar>
            </AppBar>
        </div>
    );
}

export default DenseAppBar;