import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import './main.scss';

 function SimpleContainer() {
    return (
        <React.Fragment>
            <CssBaseline />
            <Container maxWidth="100%" style={{ backgroundColor: '#424242', height: '52vh', width: "100%", position: "relative" }}>
                <div className="hero">
                    <h1>Album layout</h1>
                    <p>Something short and leading about the collection belowâ€”its contents, the creator, etc. Make it short and sweet, but not too short so folks don't simply skip over it entirely.</p>

                    <div className="wrapperBtns">
                        <Button variant="contained" color="primary" disableElevation style={{ background: "#90CAF9", color:"black" }}>
                            Main call to action
                        </Button>

                        <Button variant="outlined" color="primary">
                            Secondary action
                        </Button>
                    </div>
                </div>
            </Container>
        </React.Fragment>
    );
}

export default SimpleContainer;